import { Component, OnInit } from '@angular/core';
import {ValidateService} from "../../../services/validate.service";
import {FlashMessagesService} from 'angular2-flash-messages';
import {EventsService} from "../../../services/events.service";
import {Router} from '@angular/router';

@Component({
  selector: 'app-create-event',
  templateUrl: './create-event.component.html',
  styleUrls: ['./create-event.component.css']
})
export class CreateEventComponent implements OnInit {
  eventName:String;
  eventPoster:String;
  date:String;
  time:String;
  city:String;
  address:String;

  constructor(private validateService:ValidateService,
              private flashMessage:FlashMessagesService,
              private eventService:EventsService,
              private router:Router) {
  }

  ngOnInit() {
  }

  onCreateEventSubmint() {
    const event = {
      eventName: this.eventName,
      eventPoster: this.eventPoster,
      date: this.date,
      time: this.time,
      city: this.city,
      address: this.address
    };

    //#region Require Fields
    if (!this.validateService.validateEvent(event)) {
      this.flashMessage.show("Please fill in all fields", {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    //endregion

    //#region Save Event
    this.eventService.createEvent(event).subscribe(data=> {
      if (data.success) {
        this.flashMessage.show("Your event has been posted.", {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/dashboard']);
      } else {
        this.flashMessage.show("Something went wrong.", {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/create-event']);
      }
    })
    //endregion

  }


}



