import {Routes} from '@angular/router';
import {ProfileComponent} from "./components/profile/profile.component";
import {LoginComponent} from "./components/login/login.component";
import {RegisterComponent} from "./components/register/register.component";
import {HomeComponent} from "./components/home/home.component";
import {DasboardComponent} from "./components/dasboard/dasboard.component";
import {CreateEventComponent} from "./components/events/create-event/create-event.component";
import {EventListResolver} from "./services/resolver";
import {EventDetailsComponent} from "./components/events/event-details/event-details.component";
import {GetEventResolverService} from "./services/get-event-resolver.service";

export const appRoutes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DasboardComponent, resolve: {events: EventListResolver}},
  {path: 'profile', component: ProfileComponent},
  {path: 'create-event', component: CreateEventComponent},
  {path: 'dashboard/:_id', component: EventDetailsComponent, resolve: {event: GetEventResolverService}}
];



