import {Component, Input, OnInit, ChangeDetectorRef} from '@angular/core';
import {EventsService} from "../../../../services/events.service";
import {ValidateService} from "../../../../services/validate.service";
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from "@angular/router";

@Component({
  selector: 'app-add-session',
  templateUrl: './add-session.component.html',
  styleUrls: ['./add-session.component.css']
})
export class AddSessionComponent implements OnInit {
  @Input() eventId: String;
  sessionTitle: String;
  sessionLevel: String;
  sessionDescription: String;

  constructor(private eventService: EventsService,
              private validateService: ValidateService,
              private flashMessage: FlashMessagesService,
              private router: Router,
              private changeDetectorRef: ChangeDetectorRef) {
  }

  ngOnInit() {

  }

  onSubmitSession() {
    const session = {
      eventId: this.eventId,
      sessionTitle: this.sessionTitle,
      sessionLevel: this.sessionLevel,
      sessionDescription: this.sessionDescription
    };
    //#region Require Fields
    if (!this.validateService.validateSession(session)) {
      this.flashMessage.show("Please fill in all fields", {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    //endregion

    //#region Save Session
    this.eventService.saveSession(session).subscribe((data) => {
      if (data) {
        this.flashMessage.show("Your session has been saved.", {cssClass: 'alert-success', timeout: 3000});
        this.changeDetectorRef.detectChanges();
      } else {
        this.flashMessage.show("Something went wrong.", {cssClass: 'alert-danger', timeout: 3000});
      }
    })
    //endregion

  }

}



