import {Component, Input, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {EventsService} from "../../../services/events.service";
import {GetEventResolverService} from "../../../services/get-event-resolver.service";
import {AttendService} from "../../../services/attend.service";

@Component({
  selector: 'app-events-thumbnail',
  templateUrl: './events-thumbnail.component.html',
  styleUrls: ['./events-thumbnail.component.css']
})
export class EventsThumbnailComponent implements OnInit {
  @Input() event: any;


  constructor(private router: Router,
              private eventService: EventsService,
              private attendService: AttendService) {
  }

  ngOnInit() {
  }

  getEventId(event) {
    //const ;
    const eventIds = {
      eventId: event._id,
      userId: event.userId
    };
    this.eventService.getEvent(eventIds.eventId).subscribe(data => {
      this.router.navigate(['/dashboard/' + data._id]);
    })

  }

  // attendEventClick(event) {
  //   this.eventService.attendEvents(event).subscribe(data => console.log(data));
  // }

  toggleAttend(event) {
    if (this.userHasAttended(event)) {
      this.attendService.deleteAttend(event)
    } else {
      this.attendService.addAttend(event)
    }
  }

  userHasAttended(event) {
    return this.attendService.userHasAttended(event);
  }

}



