import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {RouterModule, Routes} from '@angular/router';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';

import {AppComponent} from './app.component';
import {NavbarComponent} from './components/navbar/navbar.component';
import {LoginComponent} from './components/login/login.component';
import {RegisterComponent} from './components/register/register.component';
import {HomeComponent} from './components/home/home.component';
import {ProfileComponent} from './components/profile/profile.component';
import {appRoutes} from './routes';
import {DasboardComponent} from './components/dasboard/dasboard.component';
import {ValidateService} from "./services/validate.service";
import {FlashMessagesModule} from "angular2-flash-messages";
import {AuthService} from "./services/auth.service";
import {EventsComponent} from './components/events/events.component';
import {EventsListComponent} from './components/events/events-list/events-list.component';
import {EventsThumbnailComponent} from './components/events/events-thumbnail/events-thumbnail.component';
import {CreateEventComponent} from './components/events/create-event/create-event.component';
import {EventsService} from "./services/events.service";
import {EventListResolver} from "./services/resolver";
import {EventDetailsComponent} from './components/events/event-details/event-details.component';
import {GetEventResolverService} from "./services/get-event-resolver.service";
import {EventTableComponent} from './components/profile/event-table/event-table.component';
import {AddSessionComponent} from './components/events/event-details/add-session/add-session.component';
import {SessionListComponent} from './components/events/event-details/session-list/session-list.component';
import {CollapsibleWellComponent} from './components/collapsible-well/collapsible-well.component';
import {AttendComponent} from './components/events/attend/attend.component';
import {AttendService} from "./services/attend.service";

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    ProfileComponent,
    DasboardComponent,
    EventsComponent,
    EventsListComponent,
    EventsThumbnailComponent,
    CreateEventComponent,
    EventDetailsComponent,
    EventTableComponent,
    AddSessionComponent,
    SessionListComponent,
    CollapsibleWellComponent,
    AttendComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule
  ],
  providers: [{
    provide: LocationStrategy,
    useClass: HashLocationStrategy
  }, ValidateService, AuthService, EventsService, EventListResolver, GetEventResolverService, AttendService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



